<template>
  <div class="main">
    <ul>
      <li
        v-for="(v,index) in jumplist"
        :key="index"
      >
        <a :href="v.herf">
          <img
            :src="v.src"
            alt=""
          >
          <span>{{v.title}}</span>
        </a>
      </li>
    </ul>
  </div>
</template>

<script>

export default {
  
  data() {
    return { 
      jumplist:[], 
      slist: [
        {  title: "vue", herf: "/case/2d.hmtl" },
        { title: "js", herf: "/" },
        {  title: "react", herf: "/" },
        { title: "css", herf: "/" },
        {  title: "vue", herf: "/" },
        { title: "js", herf: "/" },
        { title: "react", herf: "/" }
      ]
    };
  },
  created() {
    let asrc= this.slist.map((v)=>{
       v.src=require(`@/assets/${this.imgid(0,23)}.jpg`)
       return v
    })
  this.jumplist=asrc
     
  },
  methods:{
    imgid(m,n) {
     return Math.floor(Math.random()*(m-n)+n)
    }
  }
};
</script>

<style scoped lang="scss">
.main ul {
  display: flex;
  flex-wrap: wrap;
  li {
    list-style: none;
    justify-content: space-around;
    width: calc(100% / 6);
    margin-bottom: 20px;
    position: relative;
    a {
        text-decoration: none;
      cursor: pointer;
      img {
        border-radius: 5px;
        max-width: 90%;
        height: 187px;
        transition: .8s;
        transform: translateY(-10px);
        &:hover{
          transform: scale(1.1)
        }
        
      }
      span {
        position: absolute;
        font-size: 24px;
        color: #fff;
        font-weight: 300;
        top: 50%;
        left:50%;
        transform: translate(-50%, -50%);
      }
    }
  }
}
</style>


